import React, { Component } from 'react';
import './App.css';
import Square from './Square';
import data from './crosswords';

type crossword = Array<Array<string>>;

// const crossword = [
//     ['J', 'a', 'n', 'k', 'y'],
//     ['e', 'd', 'i', 'n', 'a'],
//     ['t', 'a', 'l', 'o', 'n'],
//     ['t', 'i', 'm', 'b', 's'],
//     ['a', 'r', 'a', 's', 'h'],
// ];

const crosswords: Array<crossword> = data.crosswords;
const definitions: any = data.definitions;

function getCrosswordColumnWord(crossword: crossword, columnNum: number) {
    let word = '';
    for (let i = 0; i < 5; i++) {
        word += crossword[i][columnNum];
    }
    return word;
}

function crosswordIsFull(crossword: crossword) {
    for (let i = 0; i < 5; i++) {
        for (let j = 0; j < 5; j++) {
            if (crossword[i][j] === '') {
                return false;
            }
        }
    }
    return true;
}

function crosswordsAreEqual(crossword1: crossword, crossword2: crossword) {
    for (let i = 0; i < 5; i++) {
        for (let j = 0; j < 5; j++) {
            if (crossword1[i][j] !== crossword2[i][j]) {
                return false;
            }
        }
    }
    return true;
}

const blankCrossword = [
    ['', '', '', '', ''],
    ['', '', '', '', ''],
    ['', '', '', '', ''],
    ['', '', '', '', ''],
    ['', '', '', '', ''],
];

class App extends Component {
    state = {
        highlightedRow: 0,
        highlightedColumn: 0,
        isRow: true,
        crosswordNum: 0,
        crossword: crosswords[0],
        currentCrossword: blankCrossword,
        autocheck: false,
    };

    componentDidMount() {
        document.addEventListener(
            'keydown',
            event => {
                const symbol = event.key.toLowerCase();
                const newCross = this.state.currentCrossword.slice(0);
                newCross[this.state.highlightedRow][
                    this.state.highlightedColumn
                ] = symbol;
                this.setState({
                    currentCrossword: newCross,
                });
                this.moveToNewSquare();
            },
            false
        );
    }

    moveToNewSquare = () => {
        if (this.state.isRow) {
            if (this.state.highlightedColumn !== 4) {
                this.setState({
                    highlightedColumn: this.state.highlightedColumn + 1,
                });
            } else if (this.state.highlightedRow !== 4) {
                this.setState({
                    highlightedRow: this.state.highlightedRow + 1,
                    highlightedColumn: 0,
                });
            } else {
                this.setState({
                    highlightedRow: 0,
                    highlightedColumn: 0,
                    isRow: false,
                });
            }
        } else {
            if (this.state.highlightedRow !== 4) {
                this.setState({
                    highlightedRow: this.state.highlightedRow + 1,
                });
            } else if (this.state.highlightedColumn !== 4) {
                this.setState({
                    highlightedColumn: this.state.highlightedColumn + 1,
                    highlightedRow: 0,
                });
            } else {
                this.setState({
                    highlightedRow: 0,
                    highlightedColumn: 0,
                    isRow: true,
                });
            }
        }
    };

    render() {
        const squares = [];
        for (let i = 0; i < 5; i++) {
            for (let j = 0; j < 5; j++) {
                squares.push(
                    <Square
                        key={i * 10 + j}
                        autocheck={this.state.autocheck}
                        correct={
                            this.state.currentCrossword[i][j] ===
                            this.state.crossword[i][j]
                        }
                        value={this.state.currentCrossword[i][j]}
                        superHighlighted={
                            this.state.highlightedRow === i &&
                            this.state.highlightedColumn === j
                        }
                        highlighted={
                            this.state.isRow
                                ? this.state.highlightedRow === i
                                : this.state.highlightedColumn === j
                        }
                        onClick={() => {
                            if (
                                this.state.highlightedColumn === j &&
                                this.state.highlightedRow === i
                            ) {
                                this.setState({
                                    isRow: !this.state.isRow,
                                });
                                return;
                            }
                            this.setState({
                                highlightedRow: i,
                                highlightedColumn: j,
                            });
                        }}
                    />
                );
            }
        }
        const word = this.state.isRow
            ? this.state.crossword[this.state.highlightedRow].join('')
            : getCrosswordColumnWord(
                  this.state.crossword,
                  this.state.highlightedColumn
              );
        const wordObj = definitions[word];
        const regex = new RegExp(`${wordObj.originalWord}`, 'ig');
        const definition = wordObj.definition.replace(regex, '_____');
        const example = wordObj.example.replace(regex, '_____');

        const hasCorrectCrossword = crosswordsAreEqual(
            this.state.currentCrossword,
            this.state.crossword
        );
        return (
            <div
                className="App"
                style={{
                    padding: 80,
                    width: 440,
                }}
            >
                <p>
                    Crossword Generator 3000: Crossword #
                    {this.state.crosswordNum + 1}
                </p>
                {hasCorrectCrossword && <h4>Congrats! That's correct</h4>}
                {crosswordIsFull(this.state.currentCrossword) &&
                    !hasCorrectCrossword && <h4>Sorry! That's incorrect</h4>}
                <div style={{ display: 'flex', flexWrap: 'wrap' }}>
                    {squares}
                </div>
                <p>
                    <strong>Definition: {definition}</strong>
                </p>
                <p>Example: {example}</p>
                <div>
                    <button
                        onClick={() => {
                            this.setState({
                                currentCrossword: this.state.crossword,
                            });
                        }}
                    >
                        Give me the answers
                    </button>
                    <button
                        onClick={() => {
                            if (this.state.crosswordNum === crosswords.length) {
                                alert('Thats all the crosswords we have');
                            } else {
                                this.setState({
                                    currentCrossword: blankCrossword,
                                    crosswordNum: this.state.crosswordNum + 1,
                                    crossword:
                                        crosswords[this.state.crosswordNum + 1],
                                });
                            }
                        }}
                        style={{ marginLeft: 10 }}
                    >
                        New Crossword
                    </button>
                    <button
                        onClick={() => {
                            this.setState({
                                autocheck: !this.state.autocheck,
                            });
                        }}
                        style={{ marginLeft: 10 }}
                    >
                        Turn {!this.state.autocheck ? 'on' : 'off'} autocheck
                    </button>
                </div>
            </div>
        );
    }
}

export default App;
