import React, { Component } from 'react';

export default ({
    onClick,
    highlighted,
    value,
    superHighlighted,
    autocheck,
    correct,
}: {
    onClick(): void,
    highlighted: boolean,
    value: string,
    superHighlighted: boolean,
    autocheck: boolean,
    correct: boolean,
}) => {
    let color = 'black';
    if (autocheck) {
        if (correct) {
            color = 'black';
        } else {
            color = 'rgba(214, 69, 65, 1)';
        }
    }
    return (
        <div
            onClick={onClick}
            style={{
                backgroundColor: !highlighted
                    ? 'white'
                    : superHighlighted
                    ? 'rgba(137, 196, 244, 1)'
                    : 'rgba(228, 241, 254, 1)',
                height: 80,
                width: 80,
                outline: '2px solid black',
                fontSize: 60,
                color,
            }}
        >
            {value.toUpperCase()}
        </div>
    );
};
